import { PieChart } from 'react-minimal-pie-chart';
import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useContext } from 'react';
import QuizContext from '../../context/QuizContext';
import { getReport } from '../../http';

const Analyze = () => {
  const [analyzing, setAnalyzing] = useState(true);
  const [calculating, setCalculating] = useState(true);

  const {answers, setReport, answering, setAnswering, startAnalysing, setStartAnalysing, reporting, setReporting, abilityScore, willingnessScore} = useContext(QuizContext)

  console.log(answers)

  const navigate = useNavigate()

  useEffect(() => {

    answers.current.forEach(an => {
      if(an.q_group === 'rat_ability'){
        abilityScore.current = abilityScore.current + an.weight
      }

      if(an.q_group === 'rat_willingness'){
        willingnessScore.current = abilityScore.current + an.weight
      }
      setAnalyzing(false)
    })


    const fetchReport = async () => {
      const avg = (willingnessScore.current + abilityScore.current) / 2
      const reportData = await getReport(((+willingnessScore.current) + (+abilityScore.current)) / 2);
      setReport(reportData.data)
      setCalculating(false);
      setStartAnalysing(false)
      setReporting(true)
    }
    
    setTimeout(() => {
      fetchReport()
    }, 1000)
  }, [])


    return (
        <div className="h-[90vh] flex justify-center pt-4">
          
           <div className='w-[300px]'>
           <div className='h-[300px]'>
           <PieChart 
            startAngle={200}
            
  data={[
    { title: 'One', value: 10, color: '#7875a8' },
    { title: 'Two', value: 15, color: '#c7b6dc' },
    { title: 'Three', value: 20, color: '#9e8ed5' },
    { title: 'Three', value: 20, color: '#6652a7' },
    { title: 'Three', value: 20, color: '#423f74' },
    { title: 'Three', value: 20, color: '#9a98c8' }

  ]}
/>
           </div>
   <div className='mt-4'>
    <p className='text-xl text-secondary'>{analyzing ? <span className='loader'></span> : <i className='fa fa-check'></i>} Analysing your answer</p>
    <p className='text-xl text-secondary'>{calculating ? <span className='loader'></span> : <i className='fa fa-check'> </i>}Calculating level of risk</p>
   </div>
           </div>
        </div>
    )
}

export default Analyze;