import axios from 'axios';


//https://vivek-lime.vercel.app
const api = axios.create({
    baseURL: 'https://vivek-lime.vercel.app',
    withCredentials: true,
    headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json'
    }
})


export const getQuestion = async () => api.get('/api/question/get');
export const getReport = async (score) => api.get(`/api/report/get?score=${score}`);
export const subMail = async (data) => api.post('/api/user/create', data)
