

const Card = ({title, icon}) => {
    return (
        <div className="bg-[#ffffff] text-center py-4 md:py-6 xl:py-8 rounded-xl custom-shadow">
            <p className="text-lg lg:text-xl xl:text-2xl font-bold text-secondary calibre">{title}</p>
            {
              icon && <div className="flex justify-center mt-4">
                <img className="w-[130px]" src="./assets/images/range.svg" />
              </div>
            }
        </div>
    )
}

export default Card;