import { Input } from "antd";
import { Select } from "antd";
import { useState, useEffect, useRef, useContext } from "react";
import Submit from "./Submit";
import QuizContext from "../../context/QuizContext";

let radioOpt = ['A','B','C','D','E','F','G','H','I','J','K','L','M','N','O','P','Q','R'];

const Quiz1 = ({que, qi}) => {

    const [timer, setTimer] = useState(30)

    const {queIndex, next, back, questions, setQueIndex, totalQue, answers} = useContext(QuizContext)

    const [userAnswer, setUserAnswer] = useState({_id: '', question: '', answer: '', weight:'', q_type:''});



    // useEffect(() => {
    //    if((queIndex + 1) != totalQue){
    //     setTimeout(() => {
    //         if(timer > 0 ){
    //             setTimer(timer - 1)
    //         }else if(timer == 0){
    //             setTimer(30)
    //             if(queIndex < totalQue && totalQue >= 0){
    //                 next(userAnswer)
    //             }
    //         }
    //     }, 1000)
    //    }
    // }, [timer])

    useEffect(() => {
        if(answers.current[queIndex]){
            setUserAnswer(answers.current[queIndex]);
        }
    }, [])

    const handleChnage = (e, que) => {
  
      
        if(que.q_type === 'input_box' || que.q_type === 'date'){
            const w = que.weight ? que.weight : 0;
            setUserAnswer({_id: que._id, question: que.question, answer: e.target.value, weight:w, q_group:''})
            //answers.current = [...answers.current, userAnswer]
            return
        }

        if(que.q_type === 'dropdown'){
            
            let w;
            let i = que.options.findIndex(o => o.option === e.target.value);
            if(i === -1){
                w = 0
            }else{
                w = que.options[i].weight
            }
            setUserAnswer({_id: que._id, question: que.question, answer: e.target.value, weight:w, q_group: que.q_group ? que.q_group : ''})
            //answers.current = [...answers.current, userAnswer]
            return
        }

        if(que.q_type === 'radio'){
          
            if(e.target.checked){
                let w;
                let i = que.options.findIndex(o => o.option === e.target.value);
                if(i === -1){
                    w = 0
                }else{
                    w = que.options[i].weight
                }
                setUserAnswer({_id: que._id, question: que.question, answer: e.target.value, weight: w, q_group: que.q_group ? que.q_group : ''})
            }
        }

    //     if(que.q_group && (que.q_group == 'rat_ability' || que.q_group == 'rat_willingness')){

    //     }else{
            
    //     }

    //    if(e.target){
    //     setUserAnswer(e.target.value)
    //    }else{
    //     setUserAnswer(e)
    //    }
    }

  
    
   if(que.q_type === 'input_box'){
    return (
        <div>
        <div>
        <div className="flex flex-col justify-between items-center w-full">
        <div className="w-full flex justify-center relative">
                
            </div>
            <div className="h-[100px] bg-[#e9a585a1] w-full flex justify-center items-center custom-gradient">
              <label className="label-text gt mb-2 text-white translate-x-[100px] opacity-0 from-left">{que.question}</label>
            </div>
            <div className="pb-3">
            {/* <p className="text-red-500">{timer} Second</p> */}
           </div>
            <div className="w-[80%] px-[50px]">
               <Input className="translate-x-[100px] opacity-0 from-left" placeholder="Your Name" name={que.question} value={userAnswer.answer} onChange={(e) => handleChnage(e, que)}/>
            </div>
        </div>
        </div>
        <div>
            <div className="w-full flex items-center justify-between space-x-8">

          <Submit timer={timer} userAnswer={userAnswer}/>
            </div>
            <div className="w-full bg-[#09209311] mt-[30px]  p-6 rounded-md">
                    <div className="w-full mx-auto">
                    <p className="cliber text-[16px] text-primary">Why is it important?</p>
                    <p className="cliber text-[16px] text-secondary mt-1 mb-2">{que.why}</p>
                    </div>
                </div>
        </div>
       
    </div>
    )
   }else if(que.q_type === 'date'){
    return(
        <div>
        <div>
        <div className="flex flex-col justify-between items-center w-full">
        <div className="w-full flex justify-center relative">
             
            </div>
        <div className="bg-[#e9a585a1] w-full flex justify-center items-center h-[100px] custom-gradient">
              <label className="label-text gt mb-2 translate-x-[100px] opacity-0 from-left">{que.question}</label>
            </div>
            <div className="pb-3">
            {/* <p className="text-red-500">{timer} Second</p> */}
           </div>
            <div className="w-[80%] px-[50px]">
               <Input type="date" className="translate-x-[100px] opacity-0 from-left" name={que.question} value={userAnswer.answer} onChange={(e) => handleChnage(e, que)} />
            </div>
        </div>
        </div>
        <div>
            <div className="w-full flex items-center justify-between space-x-8">

            <Submit timer={timer} userAnswer={userAnswer} />
            </div>
            <div className="w-full mt-[30px] bg-[#09209311]  p-6 rounded-md">
                    <div className="w-full mx-auto">
                    <p className="cliber text-[16px] text-primary">Why is it important?</p>
                    <p className="cliber text-[16px] text-secondary mt-1 mb-2">{que.why}</p>
                    </div>
                </div>
        </div>
       
    </div>
    )
   }else if(que.q_type === 'dropdown'){
    return (
        <div>
        <div>
        <div className="flex flex-col justify-between items-center w-[90%]">
        <div className="w-full flex justify-center relative">
               
            </div>
            <div className="bg-[#e9a585a1] w-full flex justify-center items-center h-[100px] custom-gradient">
              <label className="label-text gt mb-2 translate-x-[100px] opacity-0 from-left">{que.question}</label>
            </div>
            <div className="pb-3">
            {/* <p className="text-red-500">{timer} Second</p> */}
           </div>
            <div className="w-full">
            <select className="w-full outline-none py-2 rounded-md translate-x-[100px] opacity-0 from-left" placeholder="Select" name={que.question} onChange={(e) => handleChnage(e, que)}>
                
                {
                    que.options.map((op) => {
                        return <option selected={userAnswer.answer === op.option ? true : false} value={op.option}>{op.option}</option>
                    })
                }

              </select>
        
            </div>
        </div>
        </div>
        <div>
            <div className="w-full flex items-center justify-between">

            <Submit timer={timer} userAnswer={userAnswer} />
            </div>
            <div className="w-full bg-[#09209311] mt-[30px]  p-6 rounded-md">
                    <div className="w-full mx-auto">
                    <p className="cliber text-[16px] text-primary">Why is it important?</p>
                    <p className="cliber text-[16px] text-secondary mt-1 mb-2">{que.why}</p>
                    </div>
                </div>
        </div>
       
    </div>
    )
   }else if(que.q_type === 'radio'){
    return (
        <div>
      
        <div className="flex flex-col justify-between items-center w-full">
        <div className="w-full flex justify-center relative">
              
            </div>
           <div className="h-[100px] bg-[#e9a585a1] w-full flex justify-center items-center px-[20px] md:px-[50px] custom-gradient">
             <label className="label-text gt text-center translate-x-[100px] opacity-0 from-left">{que.question}</label>
           </div>
           <div className="pb-3">
            {/* <p className="text-red-500">{timer} Second</p> */}
           </div>
            <div className="w-full bg-white px-[50px] rounded-lg duration-300 sco">
            
                <div className="grid grid-cols-1 xl:grid-cols-2">
                {
                    que.options.map((op,i) => {
                        return (
                            <div>
                                <label htmlFor={op.option} className="">
                                    <input id={op.option} onChange={(e) => handleChnage(e, que)} className="rad hidden" type="radio" name={que.question} value={op.option} />
                                    <div className="flex items-center ml-2 border-2 rounded-lg border-[#09209344] p-4 my-2 radio_con translate-x-[100px] opacity-0 from-left" style={{animationDelay: `.${i}s`}}>
                                       <div className="w-[28px] font-bold h-[28px] mr-2 bg-primary text-white hidden radio_check rounded-md justify-center items-center radio_con">
                                           <i class="fa-solid fa-check"></i>
                                       </div>
                                       <div className="w-[28px] redio_opt_ind font-bold h-[28px] mr-2 bg-[#09209344] rounded-md flex justify-center items-center radio_con">
                                            {radioOpt[i]}
                                       </div>
                                       <span className="text-[16px] font-semibold radio_text">{op.option}</span>
                                    </div>
                                </label>
                            </div>
                        
                        )
                    })
                }
                </div>
        
            </div>
        </div>
       
        <div>
            <div className="w-full flex items-center justify-between">

            <Submit timer={timer} userAnswer={userAnswer} />
            </div>
            <div className="w-full mt-[30px] bg-[#09209311]  p-6 rounded-md">
                    <div className="w-full mx-auto">
                    <p className="cliber text-[16px] text-primary">Why is it important?</p>
                    <p className="cliber text-[16px] text-secondary mt-1 mb-2">{que.why}</p>
                    </div>
                </div>
        </div>
       
    </div>
    )
   }
}

export default Quiz1;