import Quiz1 from "./Quiz1";
import { getQuestion } from "../../http";
import { useState, useEffect, useRef } from "react";
import QuizContext from "../../context/QuizContext";
import Analyze from "./Analyze";
import Reports from './Reports'






const Quiz = () => {
    const [quizIndex, setQuizIndex] = useState(0);
    //const CurrentQuiz = quizMap[quizIndex];
    
    const [questions, setQuestion] = useState([])
    const [queIndex, setQueIndex] = useState(0)
    const [totalQue, setTotalQue] = useState(0)

    const [answering, setAnswering] = useState(true)
    const [startAnalysing, setStartAnalysing] = useState(false);
    const [reporting, setReporting] = useState(false);
    const [loading, setLoading] = useState(true)

    const abilityScore  = useRef(0)
    const willingnessScore = useRef(0)

    const [startAnalyzing, setStartAnalyzing] = useState(false);
    const [report, setReport] = useState(null)

    const answers = useRef([])

  

    

    useEffect(() => {
        const getAllQue = async () => {
            const {data} = await getQuestion();
            setQuestion(data)
            setTotalQue(data.length)
            setLoading(false)
        }
        getAllQue()
    }, [])


    const next = (userAnswer) => {
        
        if(queIndex < questions.length && queIndex >= 0){
            const index = answers.current.findIndex((c) => c._id === userAnswer._id);
            console.log(index)
            if(index === -1){
                answers.current = [...answers.current, userAnswer]
            }else{
                let upd = [...answers.current]
                upd.splice(index, 1, userAnswer);
                answers.current = upd;
            }
            
            setQueIndex((pre) => pre + 1)
        }else{
            return
        }

        console.log(answers.current)
    }

    const back = () => {
   
        if(queIndex > 0 && queIndex <= questions.length){
            setQueIndex((pre) => pre - 1)
        }else{
            return
        }
    }

    if(answering){
      if(loading){

        return (
            <div className="w-[90%] mx-auto max-w-[800px] my-[100px] min-h-[100vh] rounded-lg bg-white flex justify-center items-center shod">
                <div className="rotater"></div>
            </div>
        )

      }else{
        return (
            <QuizContext.Provider value={{
                queIndex, next, back, questions, setQueIndex, totalQue, answers, answering, setAnswering, startAnalysing, setStartAnalysing, reporting, setReporting, 
                abilityScore, willingnessScore, report, setReport
            }}>
            <div className="w-[90%] mx-auto max-w-screenlxl my-[100px] rounded-lg bg-white shod border border-primary">
           
                    <div className="w-full">
                        
                      {
                        questions.map((que, qi) => {
                            if(qi == queIndex) {
                                return <Quiz1 que={que} qi={qi}/>
                            }
                        })
                      }
                    </div>
    
           
              
            </div>
            </QuizContext.Provider>
        )
      }
    }else if(startAnalysing){
        return (
            <QuizContext.Provider value={{
                queIndex, next, back, questions, setQueIndex, totalQue, answers, answering, setAnswering, startAnalysing, setStartAnalysing, reporting, setReporting, 
                abilityScore, willingnessScore, report, setReport
            }}>
             <Analyze />
            </QuizContext.Provider>
        )
    }else if(reporting){
           return (
            <QuizContext.Provider value={{
                queIndex, next, back, questions, setQueIndex, totalQue, answers, answering, setAnswering, startAnalysing, setStartAnalysing, reporting, setReporting,
                abilityScore,  willingnessScore,
                report, setReport
            }}>
             <Reports />
            </QuizContext.Provider>
           )
    }
}

export default Quiz;